import React from 'react'
import styled from 'styled-components'

const Li = styled.li`
  padding: 5px 10px;
  margin: 4px 1px;
  border: none;
  border-radius: 25px;
  list-style: none;
`

const Reason = ({ reason }) => {
  return <Li style={{ background: `${reason.color}` }}>{reason.name}</Li>
}

export default Reason
